import {
  Box,
  Grid,
  Heading,
  Icon,
  Link,
  Spacer,
  Stack,
} from '@chakra-ui/react';
import React, { Component } from 'react';
import { TiStarburst } from 'react-icons/ti';
import config from '../../config';
class Experience extends Component {
  render() {
    const { jobs } = config;

    return (
      <Stack
        id="experience"
        bg="#F7FAFC"
        className="resume-section  d-flex justify-content-center"
        mb={10}
        borderRadius="lg"
      >
        <Heading as="h2" isTruncated pt={4} pb={4}>
          <Icon
            mr="4"
            fontSize="16"
            _groupHover={{
              color: 'white',
            }}
            as={TiStarburst}
          />{' '}
          Experience
        </Heading>
        <Grid
          gap={2}
          templateColumns={{
            base: 'repeat(1, 1fr)',
            md: 'repeat(1, 1fr)',
            lg: 'repeat(3, 1fr)',
            xl: 'repeat(4, 1fr)',
          }}
        >
          {jobs.map((job) => {
            const {
              company,
              url,
              occupation,
              description,
              period,
              responsibilities,
              projects,
            } = job;
            return (
              <Box
                maxW="sm"
                borderWidth="1px"
                borderRadius="lg"
                overflow="hidden"
                m={2}
                key={url}
                p={10}
                _hover={{ bg: 'gray.100' }}
                className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5"
              >
                <Heading size="md" color="orange">
                  {occupation}
                </Heading>
                <Link to={url}> @{company} </Link>
                <h4> {period}</h4>

                <Stack pt={5}>
                  <Heading size="sm">Responsibilities</Heading>
                  <ul className="responsibilities">
                    {responsibilities.map((responsibility) => {
                      return <li key={Math.random()}>{responsibility}</li>;
                    })}
                  </ul>
                </Stack>

                <Spacer />

                <Stack pt={5}>
                  <Heading size="sm"> Projects</Heading>
                  {projects.map((project) => {
                    return (
                      <li key={Math.random()}>
                        <a
                          href={project.url}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <i className="fa fa-globe" aria-hidden="true"></i>
                          &nbsp;
                          {project.name}
                        </a>

                        {/* <ul className="technologies">
                                {project.technologies.map((tech) => {
                                  return (
                                    <li key={Math.random()}>
                                      <span className="badge badge-secondary">
                                        {tech}
                                      </span>
                                    </li>
                                  );
                                })}
                              </ul> */}
                      </li>
                    );
                  })}
                </Stack>

                <p>{description}</p>
              </Box>
            );
          })}
        </Grid>
      </Stack>
    );
  }
}

export default Experience;
